class HeatmapArea {
  constructor(x, y, total) {
    this.startX = x;
    this.startY = y;
    this.endX = x + 32;
    this.endY = y + 24;
    this.total = total;
  }

  expandTo(x, y) {
    this.endX = Math.max(x, this.startX + 32);
    this.endY = Math.max(y, this.startY + 24);
    this.setCSS();
  }

  cointainsPoint(point) {
    return (
      (point.x >= this.startX) &&
      (point.x <= this.endX) &&
      (point.y >= this.startY) &&
      (point.y <= this.endY)
    );
  }

  setCSS() {
    if (this.el) {
      this.el.style.left = this.startX + 'px';
      this.el.style.top = this.startY + 'px';
      this.el.style.width = (this.endX - this.startX) + 'px';
      this.el.style.height = (this.endY - this.startY) + 'px';
    }
  }

  getPoints() {
    return this.points;
  }

  setPoints(n) {
    this.text.innerHTML = `${Math.floor(n * 100.0 / this.total)}%`;
  }

  createEl() {
    this.el = document.createElement('div');
    this.el.classList.add('area');
  }

  createClose() {
    this.close = document.createElement('div');
    this.close.classList.add('area__close');
    this.close.innerHTML = '×';
    this.el.append(this.close);

    this.close.addEventListener('click', (e) => {
      this.el.remove();
    });
    this.close.addEventListener('mousedown', (e) => {
      e.stopPropagation();
    })
  }

  createText() {
    this.text = document.createElement('div');
    this.text.classList.add('area__text');
    this.el.append(this.text);
  }

  render() {
    this.createEl();
    this.createText();
    this.createClose();
    this.setCSS();
    return this.el;
  }
}
export default HeatmapArea;
