<template>
  <div class="App">
    <div :class="contentClasses" @click="startLoadingFile" @drop="onDropFile" @dragenter="onDragEnter" @dragleave="onDragLeave" @dragover="onDragOver">
      <template v-if="type === 'comment'">
        <div class="App__title">{{ question }}</div>
        <div class="App__image" v-if="image_url">
          <div class="App__image-container" :style="{ backgroundImage: `url(${image_url})` }"></div>
        </div>
        <div class="App__responses">
          <div class="App__responses-item" v-for="resp in responses">
            {{ resp.comment }}
          </div>
        </div>
      </template>
      <template v-if="type === 'heatmap'">
        <div class="App__title">{{ question }}</div>
        <div class="App__heatmap">
          <div class="App__heatmap-image">
            <img :src="image_url" ref="heatmap-image" />
            <div class="App__heatmap-image-map" ref="heatmap-map"></div>
            <div class="App__heatmap-image-stats" ref="heatmap-stats"></div>
          </div>
        </div>
        <div class="App__heatmap-timeline">
          <input type="range" class="App__heatmap-timeline-range" ref="heatmap-range" min="0" :max="maxDuration" v-model="currentDuration">
          <div class="App__heatmap-timeline-current" ref="heatmap-range-current">{{maxDuration}} сек.</div>
        </div>
        <div class="App__heatmap-median">
          Среднее время клика (медиана): {{ click_median }} сек.
        </div>
      </template>
      <template v-if="type === null">
        <div class="App__upload">Нажмите для загрузки файла</div>
        <input class="App__upload-file" type="file" name="file" ref="file" @change="getFileContent" />
      </template>
    </div>
  </div>
</template>

<script>
import Heatmap from './heatmap.js'
import HeatmapArea from './heatmap_area.js'

function median(values){
  if(values.length ===0) return 0;

  values.sort(function(a,b){
    return a-b;
  });

  var half = Math.floor(values.length / 2);

  if (values.length % 2)
    return values[half];

  return (values[half - 1] + values[half]) / 2.0;
}

export default {
  name: 'App',
  data() {
    return {
      type: null,
      question: null,
      image_url: null,
      responses: null,
      heatmap: null,
      currentDuration: 0,
      maxDuration: 0,
      dragOver: false
    }
  },
  computed: {
    click_median() {
      const time = median(this.responses.map(r => r.duration));
      return Math.round(time / 10.0) / 100;
    },
    contentClasses() {
      return {
        App__content: true,
        App__content_over: this.dragOver
      }
    }
  },
  methods: {
    getFileContent() {
      const file = this.$refs.file.files[0]
      if (file) {
        this.parseAndAssignFileContent(file);
      }
    },
    startLoadingFile() {
      if (!this.type) {
        this.$refs.file.click()
      }
    },
    renderHeatmap() {
      const image = this.$refs['heatmap-image'];
      const data = this.responses.map(res => {
        const point = JSON.parse(res.point);
        return {
          left: point[0],
          top: point[1],
          time: Math.round(res.duration / 1000.0)
        }
      });

      let maxWidth;
      let maxHeight;
      data.forEach(res => {
        if (res.time > this.maxDuration) {
          this.maxDuration = res.time;
        }
      });
      this.currentDuration = this.maxDuration;

      const setHeatMapData = (time) => {
        if (!this.heatmap) {
          this.heatmap = Heatmap.create({
            container: this.$refs['heatmap-map'],
            radius: 24
          });
        }

        const mapData = data.filter(p => p.time <= time).map((point) => {
          return {
            x: Math.round(maxWidth * point.left),
            y: Math.round(maxHeight * point.top),
            value: 1
          }
        });

        window.surveyPointsData = mapData;

        this.heatmap.setData({
          max: 5,
          data: mapData
        });
      }

      image.addEventListener('load', () => {
        maxWidth = image.getBoundingClientRect().width;
        maxHeight = image.getBoundingClientRect().height;
        setHeatMapData(this.maxDuration);
      });

      const el = document.createElement('img');
      el.src = image.src;
      el.addEventListener('load', () => {
        maxWidth = image.getBoundingClientRect().width;
        maxHeight = image.getBoundingClientRect().height;
        setHeatMapData(this.maxDuration);
      });
      el.style.height = '0px';
      document.body.append(el);

      const heatmapStats = this.$refs['heatmap-stats'];
      if (heatmapStats) {
        const getY = (e) => {
          return e.clientY - heatmapStats.getBoundingClientRect().top;
        }
        const getX = (e) => {
          return e.clientX - heatmapStats.getBoundingClientRect().left;
        }

        heatmapStats.addEventListener('mousedown', (e) => {
          const newArea = new HeatmapArea(getX(e), getY(e), window.surveyPointsData.length);
          heatmapStats.append(newArea.render());

          const update = (e) => {
            newArea.expandTo(getX(e), getY(e));
            const count = window.surveyPointsData.filter(p => {
              return newArea.cointainsPoint(p)
            }).length;
            newArea.setPoints(count);
          }
          const finish = (e) => {
            update(e);
            heatmapStats.removeEventListener('mousemove', update, false);
            heatmapStats.removeEventListener('mouseup', finish, false);
          }
          heatmapStats.addEventListener('mousemove', update, false);
          heatmapStats.addEventListener('mouseup', finish, false);
        });
      }


      const filter = this.$refs['heatmap-range'];
      const display = this.$refs['heatmap-range-current'];
      const updateDisplay = () => {
        const time = parseInt(filter.value, 10);
        display.innerHTML = time + ' сек.';
        display.style.left = Math.round(time * 100.0 / parseInt(filter.max, 10)) + '%';
        setHeatMapData(time);
      }
      filter.addEventListener('change', updateDisplay);
      filter.addEventListener('input', updateDisplay);
    },
    onDropFile(e) {
      e.preventDefault();
      this.dragOver = false;
      const file = e.dataTransfer.files[0];
      this.parseAndAssignFileContent(file)
    },
    onDragEnter(e) {
      e.preventDefault();
      this.dragOver = true
    },
    onDragOver(e) {
      e.preventDefault();
      this.dragOver = true
    },
    onDragLeave(e) {
      e.preventDefault();
      this.dragOver = false
    },
    parseAndAssignFileContent(file) {
      const reader = new FileReader();
      reader.readAsText(file, "UTF-8");
      reader.onload = (evt) => {
        const data = JSON.parse(evt.target.result);
        this.type = data.answer_type;
        this.question = data.question;
        this.image_url = data.image_url;
        this.responses = data.responses;

        if (this.type == 'heatmap') {
          this.$nextTick(this.renderHeatmap)
        }
      }
    }
  }
}
</script>

<style lang="scss">
